import React, { useState } from "react";
import "./App.css";

const App = () => {
  const [firstNumber, setFirstNumber] = useState();
  const [secondNumber, setSecondNumber] = useState();
  const [thirdNumber, setThirdNumber] = useState();
  const [resultDiv, setResultDiv] = useState("resutlDiv");

  const validateInput = () => {
    const sum = parseInt(firstNumber) + parseInt(secondNumber);

    if (parseInt(sum) === parseInt(thirdNumber)) {
      document.getElementById(resultDiv).innerText = true;
    } else {
      document.getElementById(resultDiv).innerText = false;
    }
  };
  return (
    <>
      <h1>Prelim ReactJS</h1>
      <ul className="listOfInput">
        <li>
          <label for="firstNumber">First Number</label>
          <input
            type="number"
            name="firstNumber"
            placeholder="Enter First Number"
            id="firstNumber"
            onChange={(e) => {
              setFirstNumber(e.target.value);
            }}
          />
        </li>
        <li>
          <label for="secondNumber">Second Number</label>
          <input
            type="number"
            name="secondNumber"
            placeholder="Enter Second Number"
            id="secondNumber"
            onChange={(e) => {
              setSecondNumber(e.target.value);
            }}
          />
        </li>
        <li>
          <label for="thirdNumber">Third Number</label>
          <input
            type="number"
            name="thirdNumber"
            id="thirdNumber"
            placeholder="Enter Third Number"
            onChange={(e) => {
              setThirdNumber(e.target.value);
            }}
          />
        </li>
      </ul>
      <button type="submit" className="submitBtn" onClick={validateInput}>
        Submit
      </button>
      <button className="resultDiv" id={resultDiv}></button>
    </>
  );
};

export default App;
